import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-workflow-home-nav',
  templateUrl: './workflow-home-nav.component.html',
  styleUrls: ['./workflow-home-nav.component.css']
})
export class WorkflowHomeNavComponent implements OnInit {

  constructor(private router: Router, private activeRoute: ActivatedRoute) { }

  ngOnInit(): void {
  }

  navigateToHome(): void {
    this.router.navigateByUrl("/home");
  }

  navigateToCreate(): void {
    this.router.navigate(['../create'], {relativeTo: this.activeRoute});
  }

}
