import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Subscription } from '../dto/subscription';
import { SubcriptionState } from '../enums/subcription-state.enum';
import { SubscriptionService } from '../service/subcription/subscription.service';

@Injectable({
	providedIn: 'root'
})
export class WorkflowPlanValidationGuard implements CanActivate {
	constructor(private toaster: ToastrService, private subcriptionService: SubscriptionService) {

	}

	canActivate(): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		return new Promise<boolean | UrlTree>(async (success) => {
			const subscription: Subscription | null = await this.subcriptionService.getSubscription();

			if (subscription != null) {
				if (subscription.state == SubcriptionState.PENDING) {
					this.toaster.error("If you think this is a mistake please contact " + environment.help.email + " for more assistance.");
					this.toaster.error("We could not confirm the payment for " + subscription.plan?.name + " please try after sometime.");
				} else if (subscription.state == SubcriptionState.INACTIVE) {
					this.toaster.error("If you think this is a mistake please contact " + environment.help.email + " for more assistance.");
					this.toaster.error("Your subcription for " + subscription.plan?.name + " is inactivated");
				} else if (subscription.state == SubcriptionState.ACTIVE) {
					if (subscription.track != null && subscription.plan != null) {
						if (subscription.track?.workflow < subscription.plan?.workflow || subscription.plan?.workflow == -1) {
							return success(true);
						} else {
							this.toaster.error("Your subcription " + subscription.plan?.name + " only allows creation of " + subscription.plan.workflow + " workflow.");
						}

					} else {
						this.toaster.error("We could not load information related your subcription" + subscription.plan?.name + ".");
					}
					return success(false);
				}
			} else {
				this.toaster.error("If you think this is a mistake please contact " + environment.help.email + " for more assistance.");
				this.toaster.error("We could not find product subcription associated with your email address");
			}

			return success(false);

		});
	}

}
