import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { WorkflowPlanValidationGuard } from '../common/guard/workflow-plan-validation.guard';
import { WorkflowDataComponent } from './components/workflow-data/workflow-data.component';
import { WorkflowHomeComponent } from './components/workflow-home/workflow-home.component';

const routes: Routes = [
  {
    path: "",
    pathMatch: "prefix",
    children: [
      {
        path: "",
        pathMatch: "full",
        redirectTo: "dashboard"
      },
      {
        path: "dashboard",
        pathMatch: "full",
        component: WorkflowHomeComponent
      },
      {
        canActivate: [WorkflowPlanValidationGuard],
        path: "create",
        pathMatch: "full",
        component: WorkflowDataComponent
      },
      {
        path: "edit/:id",
        pathMatch: "full",
        component: WorkflowDataComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkflowRoutingModule { }
