import { Component, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Workflow } from 'src/app/common/dto/workflow';
import { Activity } from 'src/app/common/enums/activity.enum';
import { InputType } from 'src/app/common/enums/input-type.enum';
import { StringKeyVal } from 'src/app/common/interface/string-key-val';
import { CampaignService } from 'src/app/common/service/campaign/campaign.service';
import { TemplateService } from 'src/app/common/service/template/template.service';
import { EnumUtils } from 'src/app/common/utils/enum-utils';
import { WorkflowService } from '../../../common/service/workflow/workflow.service';
import { WorkflowDataFeatureComponent } from '../workflow-data-feature/workflow-data-feature.component';

@Component({
	selector: 'app-workflow-data',
	templateUrl: './workflow-data.component.html',
	styleUrls: ['./workflow-data.component.css']
})
export class WorkflowDataComponent implements OnInit {

	@ViewChildren(WorkflowDataFeatureComponent)
	private children: QueryList<WorkflowDataFeatureComponent> | null = null;

	public inputType: typeof InputType = InputType;
	public activity: typeof Activity = Activity;
	public workflow: Workflow;

	public campaignDataset: StringKeyVal[];
	public templateDataset: StringKeyVal[];
	public activityDataset: StringKeyVal[];
	public frequencyDataset: StringKeyVal[];
	public timezoneDataset: StringKeyVal[];

	public disableTemplate: boolean = false;

	constructor(private workflowService: WorkflowService, private toaster: ToastrService,
		private campaignService: CampaignService, private templateService: TemplateService,
		private router: Router, private activeRoute: ActivatedRoute, builder: FormBuilder) {
		this.workflow = new Workflow();
		this.campaignDataset = this.campaignService.getDataset();
		this.templateDataset = this.templateService.getDataset();
		this.activityDataset = EnumUtils.getActivityDataset();
		this.frequencyDataset = EnumUtils.getFrequencyDataset();
		this.timezoneDataset = EnumUtils.getTimezoneDataset();

		const id: string = activeRoute.snapshot.params.id;
		if (id != null) {
			this.loadWorkflow(id);
		}

	}

	ngOnInit(): void {

	}

	onSubmit(): void {
		const isValid: boolean | undefined = this.children?.reduce((prev: boolean, current: WorkflowDataFeatureComponent) => {
			const isCurrentChildInvalid: boolean = current.validateForm();

			if (prev == false) {
				return false;
			}

			return !isCurrentChildInvalid;

		}, true);

		if (isValid == false || isValid == undefined) {
			return;
		} else if (isValid == true && this.workflow.id != "") {
			this.workflowService.update(this.workflow).then(
				() => {
					this.toaster.success("Workflow was updated successfully. You will be redirected to Workflow Dashboard.");
					this.router.navigate(["../../dashboard"], {
						relativeTo: this.activeRoute
					});
				},
				(reason: any) => {
					this.toaster.error("Could not update workflow due to '" + reason.messsage + "'.");
				}
			)
		} else if (isValid == true && this.workflow.id == "") {
			this.workflowService.create(this.workflow).then(
				() => {
					this.toaster.success("Workflow was created successfully. You will be redirected to Workflow Dashboard.");
					this.router.navigate(["../dashboard"], {
						relativeTo: this.activeRoute
					});
				},
				(reason: any) => {
					this.toaster.error("Could not created workflow due to '" + reason.messsage + "'.");
				}
			)
		}
	}

	onValueChange(controlValue: any): void {
		this.workflow = { ...this.workflow, ...controlValue };
	}

	loadWorkflow(id: string): void {
		this.workflowService.getById(id).then(
			(workflow: Workflow) => {
				this.workflow = workflow;
				this.updateChildrenValues(this.workflow);
			},
			(reason: any) => {
				this.toaster.error(reason.message);
			}
		);
	}

	private updateChildrenValues(workflow: Workflow): void {
		this.children?.forEach((component: WorkflowDataFeatureComponent) => {
			if (component.inputType == InputType.Time) {
				component.elementForm.patchValue({
					hour: parseInt(workflow.time.hour as unknown as string),
					min: parseInt(workflow.time.min as unknown as string),
					meridiem: workflow.time.meridiem,
				});
			} else {
				component.elementForm.patchValue({
					[component.text] : workflow[component.key]
				});
			}

		});
	}

}
