import moment from "moment-timezone";
import { Activity } from "../enums/activity.enum";
import { Frequency } from "../enums/frequency.enum";
import { StringKeyVal } from "../interface/string-key-val";

export class EnumUtils {

    public static getActivityDataset(): StringKeyVal[] {
        const dataset: StringKeyVal[] = [];

        const connect: StringKeyVal = {
            key: Activity.Connection,
            value: Activity.Connection 
        }

        const message: StringKeyVal = {
            key: Activity.Message,
            value: Activity.Message
        }

        const visit: StringKeyVal = {
            key: Activity.Visit,
            value: Activity.Visit
        }

        dataset.push(connect, message, visit);

        return dataset;
    }

    public static getFrequencyDataset(): StringKeyVal[] {
        const dataset: StringKeyVal[] = [];

        const once: StringKeyVal = {
            key: Frequency.Once,
            value: Frequency.Once 
        }

        const daily: StringKeyVal = {
            key: Frequency.Daily,
            value: Frequency.Daily 
        }

        const weekdays: StringKeyVal = {
            key: Frequency.Weekdays,
            value: Frequency.Weekdays
        }

        const weekends: StringKeyVal = {
            key: Frequency.Weekends,
            value: Frequency.Weekends
        }

        dataset.push(daily, weekdays, weekends);

        return dataset;
    }

    public static getTimezoneDataset(): StringKeyVal[] {
        const dataset: StringKeyVal[] = [];

        moment.tz.names().forEach((value: string) => {
            const timezone: StringKeyVal = {
                key: value,
                value: value 
            }

            dataset.push(timezone);
        })

        return dataset;
    }
}
