import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { PartialObserver } from 'rxjs';
import { InputType } from 'src/app/common/enums/input-type.enum';
import { Meridiem } from 'src/app/common/enums/meridiem.enum';
import { StringKeyVal } from 'src/app/common/interface/string-key-val';
import { DateUtils } from 'src/app/common/utils/date-utils';

@Component({
  selector: 'app-workflow-data-feature',
  templateUrl: './workflow-data-feature.component.html',
  styleUrls: ['./workflow-data-feature.component.css']
})
export class WorkflowDataFeatureComponent implements OnInit {

  @Input("text")
  public text: string = "";

  @Input("type")
  public inputType: InputType = InputType.None;

  @Input("dataset")
  public dataset: StringKeyVal[] = [];

  @Input("key")
  public key: string = "";

  @Input("errorMessage")
  public errorMessage: string = "";

  @Input("isDisabled")
  public isDisabled: boolean = false;

  @Output("onValueChange")
  public valueChangeEvent: EventEmitter<any>;

  public elementForm: FormGroup;

  public input: typeof InputType = InputType;

  public showError: boolean = false;

  constructor(builder: FormBuilder) {
    this.elementForm = builder.group({});

    this.valueChangeEvent = new EventEmitter<any>();
  }

  ngOnInit(): void {
    if (this.inputType === this.input.Time) {

      const hour: AbstractControl = new FormControl("12", [Validators.required, Validators.min(1), Validators.max(12)]);
      const min: AbstractControl = new FormControl(0, [Validators.required, Validators.min(0), Validators.max(60)])
      const meridiem: AbstractControl = new FormControl(Meridiem.AM, [Validators.required]);

      this.elementForm.addControl("hour", hour);
      this.elementForm.addControl("min", min);
      this.elementForm.addControl("meridiem", meridiem);
      this.elementForm.valueChanges.subscribe(this.valueChange())

    } else if (this.inputType === this.input.Combobox) {

      const combobox: AbstractControl = new FormControl(null, [Validators.required]);
      combobox.valueChanges.subscribe(this.valueChange());
      this.elementForm.addControl(this.text, combobox);

    } else if (this.inputType === this.input.Number) {

      const number: AbstractControl = new FormControl(null, [Validators.required]);
      number.valueChanges.subscribe(this.valueChange());
      this.elementForm.addControl(this.text, number);

    } else if (this.inputType === this.input.Text) {
      const text: AbstractControl = new FormControl(null, [Validators.required]);
      text.valueChanges.subscribe(this.valueChange());
      this.elementForm.addControl(this.text, text);
    }

    this.elementForm.updateValueAndValidity();
  }

  valueChange(): PartialObserver<any> {
    const valueChange: PartialObserver<any> = {
      next: (value: any) => {
        const payload: any = {};
        payload[this.key] = value;
        this.valueChangeEvent.emit(payload);
      }
    }
    return valueChange;
  }

  validateForm(): boolean {
    if(this.inputType === InputType.Time && 
      (this.elementForm.controls.hour.errors != null 
        || this.elementForm.controls.min.errors != null 
        || this.elementForm.controls.meridiem.errors != null)) {
        this.showError = true;
    } else if (this.inputType !== InputType.Time && this.elementForm.controls[this.text].errors != null) {
      this.showError = true;
    } else {
      this.showError = false;
    }

    return this.showError;
  }

}
