import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Workflow } from '../../dto/workflow';
import { UserService } from '../user/user.service';
import firebase from 'firebase';
import { StringKeyVal } from '../../interface/string-key-val';
import { SchedulerService } from '../scheduler/scheduler.service';
import { SubscriptionService } from '../subcription/subscription.service';
import { TrackingType } from '../../enums/tracking-type.enum';

@Injectable({
	providedIn: 'root'
})
export class WorkflowService {

	private workflowList: Workflow[];
	private dataset: StringKeyVal[];

	constructor(private userService: UserService, private firestore: AngularFirestore,
		private scheduler: SchedulerService, private subscriptionService: SubscriptionService) {
		this.workflowList = [];
		this.dataset = [];
	}

	getAllWorkflow(): Workflow[] {
		if (this.workflowList.length === 0) {
			this.loadAllWorkflow();
		}

		return this.workflowList;
	}

	async loadAllWorkflow(): Promise<boolean> {
		const uid: string | null = await this.userService.getActiveUserId();

		if (uid == null) {
			return Promise.reject({
				message: "User session is not valid. Please Login and try again."
			});
		}

		this.firestore.collection("workflow", ref => ref.where("uid", "==", uid)
			.orderBy("created", "desc"))
			.get()
			.subscribe({
				next: (result: firebase.firestore.QuerySnapshot<unknown>) => {
					this.workflowList.splice(0);
					result.forEach((snapshot: firebase.firestore.QueryDocumentSnapshot<unknown>) => {
						const workflow: Workflow = snapshot.data() as Workflow;
						workflow.id = snapshot.id;
						this.workflowList.push(workflow);
					});
				}
			});


		return Promise.resolve(true);

	}

	create(workflow: Workflow): Promise<boolean> {
		return new Promise<boolean>(async (resolve, reject) => {
			const uid: string | null = await this.userService.getActiveUserId();

			if (uid == null) {
				return reject({ message: "User session is not valid. Please Login and try again." });
			} else {
				workflow.uid = uid;
				workflow.created = new Date().getTime();
			}

			this.firestore.collection("workflow").add(workflow).then(
				(value: firebase.firestore.DocumentReference<unknown>) => {
					this.subscriptionService.track(uid, TrackingType.WORKFLOW, 1);
					if (this.workflowList.length == 0) {
						this.loadAllWorkflow();
					} else {
						workflow.id = value.id;
						const data: StringKeyVal = {
							key: workflow.id,
							value: workflow.name
						};
						this.workflowList.splice(0, 0, workflow);
						this.dataset.splice(0, 0, data);
					}

					this.scheduler.updateState();
					return resolve(true);
				},
				(reason: any) => {
					return reject(reason);
				}
			)

		});
	}

	update(workflow: Workflow): Promise<boolean> {
		return new Promise<boolean>(async (resolve, reject) => {
			const uid: string | null = await this.userService.getActiveUserId();

			if (uid == null) {
				return reject({ message: "User session is not valid. Please Login and try again." });
			} else {
				workflow.created = new Date().getTime();
			}

			this.firestore.collection("workflow").doc(workflow.id).set(workflow).then(
				() => {

					if (this.workflowList.length == 0) {
						this.loadAllWorkflow();
					} else {
						let index: number = this.workflowList.findIndex(existingWorkflow => existingWorkflow.id === workflow.id);
						if (index != -1) {
							this.workflowList.splice(index, 1);
							this.workflowList.splice(0, 0, workflow);
						}

						index = this.dataset.findIndex(existingDatapoint => existingDatapoint.key === workflow.id);
						if (index != -1) {
							const data: StringKeyVal = {
								key: (workflow.id || 'MOCK-ID'),
								value: workflow.name
							};

							this.dataset.splice(index, 1);
							this.dataset.splice(0, 0, data);
						}

					}
					this.scheduler.updateState();
					return resolve(true);
				},
				(reason: any) => {
					return reject(reason);
				}
			)

		});
	}

	getById(id: string): Promise<Workflow> {
		return new Promise<Workflow>(async (resolve, reject) => {
			const uid: string | null = await this.userService.getActiveUserId();

			if (uid == null) {
				return reject({ message: "User session is not valid. Please Login and try again." });
			}

			this.firestore.collection("workflow")
				.doc(id)
				.get()
				.subscribe({
					next: (result: firebase.firestore.DocumentSnapshot<unknown>) => {
						const workflow: Workflow = result.data() as unknown as Workflow;

						if (result.exists && workflow.uid === uid) {
							workflow.id = result.id;
							return resolve(workflow);
						}
						return reject({
							message: "Could not find the template on server. Please try again after some time."
						})
					},
					error: (reason: any) => {
						return reject({
							message: "Could not load template from server. Please try again after some time."
						})
					}
				})

		});

	}
}
