import { Activity } from "../enums/activity.enum";
import { Time } from "./time";

export class Workflow {
    id: string = "";
    uid: string = "";
    name: string = "";
    campaignId: string = "";
    templateId: string = "";
    activity: Activity = Activity.None;
    loadCount: number = 0;
    frequency: string = "";
    time: Time = new Time();
    nextExecution: string = "NEVER";
    lastExecution: string = "NEVER"
    timezone: string = "";
    created: number = 0;
    isProcessing: boolean = false;
    [key: string]: any
}
